/* Post Card Styling */
.post-card {
    width: 250px;
    /* border: 1px solid #e1e4e8; */
    border-radius: 8px;
    margin: 10px;
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box; /* Add box-sizing to include padding in height */
    transition: 1s box-shadow;
    position: relative;
    background: white;
}
.post-card img {
    width: 100%;
    height: 150px; /* Set a fixed height for the image */
    object-fit: contain; /* Cover the image area without distortion */
    border-bottom: 1px solid #e1e4e8;
    padding-bottom: 16px;
}

.post-card h3 {
    font-size: 1.25rem;
    margin: 16px 0 0 0;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2; /* Number of lines to show */
    -webkit-box-orient: vertical;
    line-height: 1.5em; /* Adjust as necessary */
    max-height: 3em; /* 2 lines * line-height */
    height: 3em; /* Ensure title space is consistent */
}

.scroll-container {
    display: flex;
    overflow-x: auto;
    gap: 10px;
}

.post-card a {
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* Ensure proper spacing in the link */
    height: 100%; /* Ensure link height covers the card */
}
