/* Load More Button */
.load-more-button {
    display: inline-block;
    margin-top: 16px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 1.5rem;
    transition: background-color 0.3s ease;
}

.load-more-button:hover {
    background-color: #0056b3;
}

/* Responsive Design */
@media (max-width: 768px) {
    .post-card {
        width: 200px;
    }
}

@media (max-width: 480px) {
    .post-card {
        width: 150px;
    }
}
